import screenRequirements from '../data/screenRequirements.json';
import backendConfig from '../config/backend.json';

// ĞŸĞ¾Ğ»ÑƒÑ‡Ğ°ĞµĞ¼ Ğ¾Ğ±Ñ‰Ğ¸Ğµ Ğ½Ğ°ÑÑ‚Ñ€Ğ¾Ğ¹ĞºĞ¸ Ğ¸Ğ· Ğ½Ğ¾Ğ²Ğ¾Ğ³Ğ¾ Ñ„Ğ¾Ñ€Ğ¼Ğ°Ñ‚Ğ°
const commonSettings = screenRequirements.commonSettings;
const screens = screenRequirements.screens;

// ĞšĞ¾Ğ½Ğ²ĞµÑ€Ñ‚Ğ¸Ñ€ÑƒĞµĞ¼ Ğ¼Ğ°ĞºÑĞ¸Ğ¼Ğ°Ğ»ÑŒĞ½Ñ‹Ğ¹ Ñ€Ğ°Ğ·Ğ¼ĞµÑ€ Ğ¸Ğ· MB Ğ² Ğ±Ğ°Ğ¹Ñ‚Ñ‹
const maxFileSizeBytes = commonSettings.maxSize * 1024 * 1024;

// Ğ¤ÑƒĞ½ĞºÑ†Ğ¸Ñ Ğ´Ğ»Ñ Ğ²Ğ°Ğ»Ğ¸Ğ´Ğ°Ñ†Ğ¸Ğ¸ Ğ²Ğ¸Ğ´ĞµĞ¾ Ğ½Ğ° ÑĞµÑ€Ğ²ĞµÑ€Ğµ
const validateVideoOnServer = async (file) => {
  if (!backendConfig.ENABLE_BACKEND_VALIDATION) {
    return null;
  }

  try {
    const formData = new FormData();
    formData.append('file', file);

    const response = await fetch(
      `${backendConfig.BACKEND_URL}${backendConfig.ENDPOINTS.VALIDATE_VIDEO}`,
      {
        method: 'POST',
        body: formData,
        headers: {
          'ngrok-skip-browser-warning': 'true'
        }
      }
    );

    if (!response.ok) {
      console.error('ĞÑˆĞ¸Ğ±ĞºĞ° Ğ²Ğ°Ğ»Ğ¸Ğ´Ğ°Ñ†Ğ¸Ğ¸ Ğ½Ğ° ÑĞµÑ€Ğ²ĞµÑ€Ğµ:', response.status);
      return null;
    }

    const result = await response.json();
    return result;
  } catch (error) {
    console.error('ĞÑˆĞ¸Ğ±ĞºĞ° Ğ¿Ñ€Ğ¸ Ğ¾Ğ±Ñ€Ğ°Ñ‰ĞµĞ½Ğ¸Ğ¸ Ğº ÑĞµÑ€Ğ²ĞµÑ€Ñƒ:', error);
    return null;
  }
};

export const validateVideo = (file) => {
  return new Promise((resolve, reject) => {
    // ĞŸÑ€Ğ¾Ğ²ĞµÑ€ĞºĞ° Ñ€Ğ°Ğ·Ğ¼ĞµÑ€Ğ° Ñ„Ğ°Ğ¹Ğ»Ğ°
    const fileSizeValid = file.size <= maxFileSizeBytes;
    
    // Ğ¡Ğ¾Ğ·Ğ´Ğ°Ñ‘Ğ¼ Ğ²Ğ¸Ğ´ĞµĞ¾ ÑĞ»ĞµĞ¼ĞµĞ½Ñ‚ Ğ´Ğ»Ñ Ğ¿Ğ¾Ğ»ÑƒÑ‡ĞµĞ½Ğ¸Ñ Ğ¼ĞµÑ‚Ğ°Ğ´Ğ°Ğ½Ğ½Ñ‹Ñ…
    const video = document.createElement('video');
    video.preload = 'metadata';
    
    video.onloadedmetadata = () => {
      window.URL.revokeObjectURL(video.src);
      
      const duration = Math.round(video.duration);
      const width = video.videoWidth;
      const height = video.videoHeight;
      
      // ĞŸÑ€Ğ¾Ğ²ĞµÑ€ĞºĞ° Ğ´Ğ»Ğ¸Ñ‚ĞµĞ»ÑŒĞ½Ğ¾ÑÑ‚Ğ¸
      const durationValid = commonSettings.allowedDurations.includes(duration);
      
      // ĞŸÑ€Ğ¾Ğ²ĞµÑ€ĞºĞ° Ñ€Ğ°Ğ·Ñ€ĞµÑˆĞµĞ½Ğ¸Ñ - Ğ¸Ñ‰ĞµĞ¼ Ğ¿Ğ¾Ğ´Ñ…Ğ¾Ğ´ÑÑ‰Ğ¸Ğ¹ ÑĞºÑ€Ğ°Ğ½
      const matchingScreen = screens.find(
        screen => screen.width === width && screen.height === height
      );
      const resolutionValid = matchingScreen !== undefined;
      
      const validationResult = {
        isValid: fileSizeValid && durationValid && resolutionValid,
        fileSize: file.size,
        fileSizeValid,
        duration,
        durationValid,
        width,
        height,
        resolutionValid,
        fileName: file.name,
        fileType: file.type,
        matchingScreen: matchingScreen || null
      };
      
      resolve(validationResult);
    };
    
    video.onerror = () => {
      reject(new Error('ĞĞµ ÑƒĞ´Ğ°Ğ»Ğ¾ÑÑŒ Ğ·Ğ°Ğ³Ñ€ÑƒĞ·Ğ¸Ñ‚ÑŒ Ğ²Ğ¸Ğ´ĞµĞ¾'));
    };
    
    video.src = URL.createObjectURL(file);
  });
};

export const getVideoMetadata = async (file) => {
  try {
    const validation = await validateVideo(file);
    
    // ĞŸĞ¾Ğ»ÑƒÑ‡Ğ°ĞµĞ¼ Ñ€Ğ°ÑÑˆĞ¸Ñ€ĞµĞ½Ğ¸Ğµ Ñ„Ğ°Ğ¹Ğ»Ğ°
    const fileExtension = file.name.split('.').pop().toLowerCase();
    
    // ĞŸÑ€Ğ¾Ğ²ĞµÑ€ÑĞµĞ¼ ĞºĞ¾Ğ½Ñ‚ĞµĞ¹Ğ½ĞµÑ€
    const containerValid = fileExtension === commonSettings.container;
    
    // Ğ˜Ğ½Ñ„Ğ¾Ñ€Ğ¼Ğ°Ñ†Ğ¸Ñ Ğ¾ Ğ¿Ğ¾Ğ´Ñ…Ğ¾Ğ´ÑÑ‰ĞµĞ¼ ÑĞºÑ€Ğ°Ğ½Ğµ
    const screenInfo = validation.matchingScreen 
      ? `${validation.matchingScreen.name}${validation.matchingScreen.nightVersion ? ' (Ñ‚Ñ€ĞµĞ±ÑƒĞµÑ‚ÑÑ Ğ½Ğ¾Ñ‡Ğ½Ğ°Ñ Ğ²ĞµÑ€ÑĞ¸Ñ)' : ''}`
      : 'ĞĞµ Ğ½Ğ°Ğ¹Ğ´ĞµĞ½ Ğ¿Ğ¾Ğ´Ñ…Ğ¾Ğ´ÑÑ‰Ğ¸Ğ¹ ÑĞºÑ€Ğ°Ğ½';
    
    // ĞŸĞ¾Ğ¿Ñ‹Ñ‚ĞºĞ° Ğ¿Ğ¾Ğ»ÑƒÑ‡Ğ¸Ñ‚ÑŒ Ğ´Ğ°Ğ½Ğ½Ñ‹Ğµ Ñ ÑĞµÑ€Ğ²ĞµÑ€Ğ°
    const serverValidation = await validateVideoOnServer(file);
    
    // Ğ•ÑĞ»Ğ¸ ÑĞµÑ€Ğ²ĞµÑ€ Ğ²ĞµÑ€Ğ½ÑƒĞ» Ğ´Ğ°Ğ½Ğ½Ñ‹Ğµ, Ğ¸ÑĞ¿Ğ¾Ğ»ÑŒĞ·ÑƒĞµĞ¼ Ğ¸Ñ… Ğ´Ğ»Ñ ĞºĞ¾Ğ´ĞµĞºĞ° Ğ¸ FPS
    let codec = commonSettings.codec.toUpperCase();
    let codecStatus = 'success';
    let fps = commonSettings.fps.toString();
    let fpsStatus = 'success';
    let isValidFinal = validation.isValid && containerValid;
    
    if (serverValidation) {
      console.log('ĞÑ‚Ğ²ĞµÑ‚ ÑĞµÑ€Ğ²ĞµÑ€Ğ°:', serverValidation);
      
      // Ğ¡ĞµÑ€Ğ²ĞµÑ€ Ğ²Ğ¾Ğ·Ğ²Ñ€Ğ°Ñ‰Ğ°ĞµÑ‚: { valid, codec, fps, errors }
      codec = serverValidation.codec?.toUpperCase() || codec;
      fps = serverValidation.fps?.toString() || fps;
      
      // ĞŸÑ€Ğ¾Ğ²ĞµÑ€ÑĞµĞ¼ Ğ²Ğ°Ğ»Ğ¸Ğ´Ğ½Ğ¾ÑÑ‚ÑŒ Ğ½Ğ° Ğ¾ÑĞ½Ğ¾Ğ²Ğµ Ğ¾Ñ‚Ğ²ĞµÑ‚Ğ° ÑĞµÑ€Ğ²ĞµÑ€Ğ°
      const serverValid = serverValidation.valid === true;
      
      // ĞŸÑ€Ğ¾Ğ²ĞµÑ€ÑĞµĞ¼ ĞºĞ¾Ğ´ĞµĞº
      const codecMatch = serverValidation.codec?.toLowerCase() === commonSettings.codec.toLowerCase();
      codecStatus = codecMatch ? 'success' : 'error';
      
      // ĞŸÑ€Ğ¾Ğ²ĞµÑ€ÑĞµĞ¼ FPS (Ğ´Ğ¾Ğ¿ÑƒÑĞº Â±0.5)
      const actualFps = parseFloat(serverValidation.fps || 0);
      const expectedFps = commonSettings.fps;
      const fpsMatch = Math.abs(actualFps - expectedFps) < 0.5;
      fpsStatus = fpsMatch ? 'success' : 'error';
      
      // ĞĞ±Ñ‰Ğ°Ñ Ğ²Ğ°Ğ»Ğ¸Ğ´Ğ½Ğ¾ÑÑ‚ÑŒ
      isValidFinal = validation.isValid && containerValid && serverValid;
      
      console.log(`ĞšĞ¾Ğ´ĞµĞº: ${codec} (${codecStatus}), FPS: ${fps} (${fpsStatus}), serverValid: ${serverValid}`);
    }
    
    return {
      duration: `${validation.duration} ÑĞµĞºÑƒĞ½Ğ´`,
      durationStatus: validation.durationValid ? 'success' : 'error',
      container: `.${fileExtension}`,
      containerStatus: containerValid ? 'success' : 'error',
      codec: codec,
      codecStatus: codecStatus,
      fps: fps,
      fpsStatus: fpsStatus,
      resolution: `${validation.width}x${validation.height} px`,
      resolutionStatus: validation.resolutionValid ? 'success' : 'error',
      fileSize: validation.fileSize,
      fileSizeValid: validation.fileSizeValid,
      containerValid: containerValid,
      isValid: isValidFinal,
      screenInfo: screenInfo,
      matchingScreen: validation.matchingScreen,
      serverValidation: serverValidation // Ğ”Ğ»Ñ Ğ¾Ñ‚Ğ»Ğ°Ğ´ĞºĞ¸
    };
  } catch (error) {
    console.error('ĞÑˆĞ¸Ğ±ĞºĞ° Ğ¿Ñ€Ğ¸ Ğ¿Ğ¾Ğ»ÑƒÑ‡ĞµĞ½Ğ¸Ğ¸ Ğ¼ĞµÑ‚Ğ°Ğ´Ğ°Ğ½Ğ½Ñ‹Ñ…:', error);
    throw error;
  }
};

export const formatFileSize = (bytes) => {
  if (bytes === 0) return '0 Bytes';
  const k = 1024;
  const sizes = ['Bytes', 'KB', 'MB', 'GB'];
  const i = Math.floor(Math.log(bytes) / Math.log(k));
  return Math.round(bytes / Math.pow(k, i)) + ' ' + sizes[i];
};

// Ğ­ĞºÑĞ¿Ğ¾Ñ€Ñ‚Ğ¸Ñ€ÑƒĞµĞ¼ Ñ„ÑƒĞ½ĞºÑ†Ğ¸Ğ¸ Ğ´Ğ»Ñ Ğ¿Ğ¾Ğ»ÑƒÑ‡ĞµĞ½Ğ¸Ñ Ñ‚Ñ€ĞµĞ±Ğ¾Ğ²Ğ°Ğ½Ğ¸Ğ¹
export const getRequirements = () => ({
  maxFileSize: maxFileSizeBytes,
  maxFileSizeMB: commonSettings.maxSize,
  allowedDurations: commonSettings.allowedDurations,
  allowedFormats: [`video/${commonSettings.container}`],
  allowedContainers: [commonSettings.container],
  fps: commonSettings.fps,
  codec: commonSettings.codec
});

export const getScreens = () => screens;

export const getScreenByResolution = (width, height) => {
  return screens.find(screen => screen.width === width && screen.height === height);
};

